*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Oswald';
}
.container{
    background-color: lightblue;
    width: 100%;
    height: 100vh;
    display: flex;
}
.time-header{
    text-align: center;
    height: auto;
    background-color: lightsalmon;
    width: 300px;
    margin: 0 auto;
    margin-bottom: 30px;
    border-radius: 40px;
    border: 2px solid red;
}
.content{
    width: 50%;
    height: auto;
    margin: auto;
    background-color: lightgreen;
    margin-bottom: 350px;
    border-radius: 40px;
    box-shadow: 0px 0px 4px 2px red;
    padding: 8px;
}
.header{
    text-align: center;
    color:darkblue;
}
.info{
    display: flex;
    justify-content: center;
    align-items: center;
}
.input{
    padding: 6px;
    width: 50%;
    border-radius: 20px;
    padding: 6px;
    font-size: 1.3rem;
    font-weight: bolder;
}
.list{
    display: flex;
    justify-content: center;
    align-items: center;
}
.list ul{
    font-weight: bolder;
    font-size: 1.3rem;
}
.icons{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;
    border-radius: 60px;
    background-color: lightpink;
    cursor: pointer;
    padding: 0 10px;
    margin: 0 10px;
    border: 2px solid black;
    
}
.icons:hover{
    background-color: green;
    color: white;
}

.list{
    margin-top: 20px;
}
.todo_style{
    display: flex;
    justify-content: left;
    align-items: center;
}
.todo_style .cross{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    width: 25px;
    margin: 0 24px;
    background-color: lightpink;
    border-radius: 60px;
    cursor: pointer;
    padding: 0 8px;
    border: 2px solid black;
}
.todo_style .cross:hover{
    background-color: red;
    color: white;
}

@media (max-width:1000px){
    .time-header{
        width: auto;
    }
}
